.search_box{
    width: 100%;
    position: relative;
}
  
.search_box input[type="search"]{
width: 100%;
padding: 20px 200px;
box-sizing: border-box;
background:rgb(241, 241, 241) ;
border: 1px solid rgb(232,110,108);
border-radius: 2rem;
font-size: 21px;
color: rgb(232,110,108);
outline: none;
box-sizing: border-box;
transition: .5s;
}

.search_box input[type="search"]::-webkit-input-placeholder{
   color: rgb(232,110,108);
}

.search_box input[type="search"]:-ms-input-placeholder{
   color: rgb(232,110,108);
}

.search_box input[type="search"]::-ms-input-placeholder{
   color: rgb(232,110,108);
}

.search_box input[type="search"]::placeholder{
   color: rgb(232,110,108);
}



